import React from "react";

const ComponentDidMount = () => (
  <section id="component-did-mount">
    <h2><code>componentDidMount()</code></h2>
    <h4>montage</h4>
    <br />
    <p>appele immdiatement aprs que le composant est mont (insr dans le DOM)<br />
    Le bon endroit pour faire des appels AJAX et charger les donnes dans le composant.<br />
    Utile galement pour la dclaration d'un eventListener.</p>
    <pre><code className="hljs" data-trim contentEditable>{`
    class MyClass extends Component {
      constructor(props) {
        super(props);
        this.state = {
          data: "Chargement..."
        };
      }

      componentDidMount() {
        // appel API, etc. puis mise  jour du state
        this.setState({ data: /* rponse de l'API */ });
      }

      render() {
        return <div>{this.state.data}</div>;
      }
    }
    `}</code></pre>
  </section>
);

export default ComponentDidMount;
