import React from "react";

export default function Events(){
  return(
    <section id="events-handling">
      <h2>Gestion des vnements 1/2</h2>
      <p>en HTML, l'vnement sur le clic d'un lien pourrait s'crire :</p>
      <pre><code className="hljs" data-trim contentEditable>{`
    <a href="#" onclick="console.log('Le lien a t cliqu.')">
      Clique ici
    </a>
    `}</code></pre>
    <br />
    <p>Voici sa traduction en React :</p>
    <pre><code className="hljs" data-trim contentEditable>{`
    function ActionLink() {
      function handleClick(e) { // la mthode recoit l'evenement synthtique (e)
        e.preventDefault();
        console.log('Le lien a t cliqu.');
      }

      return (
        <a href="#" onClick={handleClick}> // notation camelCase sur les events
          Clique ici
        </a>
      );
    }
    `}</code></pre>
    </section>
  )
}