import React from "react";

export default function EventsBind(){
  return(
    <section id="events-binding">
      <h2>Gestion des vnements 2/2</h2>
      <p>La mthode <code>handleClick</code> doit tre <i>binde</i> pour pouvoir utiliser <code>this</code><br />
      En JS, les mthodes de classes ne sont pas lies par dfaut.</p>
      <pre><code className="hljs" data-trim contentEditable>{`
    class Toggle extends React.Component {
      constructor(props) {
        super(props);
        this.state = {isToggleOn: true};

        // Cette liaison est ncssaire afin de permettre
        // l'utilisation de this dans la fonction de rappel.
        this.handleClick = this.handleClick.bind(this);
      }

      handleClick() {
        this.setState({
          isToggleOn: !this.state.isToggleOn
        });
      }

      render() {
        return (
          <button onClick={this.handleClick}>
            {this.state.isToggleOn ? 'ON' : 'OFF'}
          </button>
        );
      }
    }
    `}</code></pre>
    </section>
  )
}