import React from 'react';

const Child = () => (
  <section id="child">
    <h2>Modle Courant 2/2</h2>
    <p>Le <b>state</b> du parent est une <b>proprit</b> de l'enfant.<br />
      Permet de sparer la logique du rendu.</p>
      <h4>Composant Enfant : contient le rendu</h4>

    <pre><code className="hljs" data-trim contentEditable>{`
  export default class ChildComposant extends Component {
    render() {
      return(
        <div>
          <div>{this.props.counter}</div>

          <button onClick={this.props.incrementCounter}>
            Increment
          </button>
        </div>
      );
    }
  }
    `}</code></pre>

    <div className="fragment">
      <br />
      <span className="blue-grey"><em>L'vnement sur le bouton va modifier la proprit </em><strong>counter</strong><em> en passant par le composant parent</em></span>
    </div>
  </section>
);

export default Child;
